/* fonts.scss */

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/* 100 Thin / 200 Extra Light / 300 Light / 400 Regular / 500 Medium / 600 Semibold / 700 Bold / 800 Extra Bold / 900 Black */

@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins/Poppins-Bold.ttf");
  font-weight:700;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins/Poppins-Medium.ttf");
  font-weight: 500;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins/Poppins-Light.ttf");
  font-weight: 300;
}

$font-main: "Poppins", sans-serif;
$font-heading: "Poppins", sans-serif;
//$font-alt: "Mobtserrat", sans-serif;


body {
  font-family: $font-main;
  color: black;
}
h1, h2, h3, h4, h5, h6, p, br, ul, li {
  margin: 0;
  font-weight: 400;
  line-height: 1.5;
}
br {
  height: 10px;
}



h1 { //page title
	font-family: $font-heading;
  font-size: 27px;
  line-height: 1.2;
}
h2 { //logo, project title
  font-family: $font-heading;
  font-size: 24px;
  line-height: 1.2;
}
h3 { //title
	font-size: 20px;
  letter-spacing: 0.5px;
}
h4 { //subtitle(+p), content(+h5 description as title)
	font-size: 18px;
  letter-spacing: 0.5px;
}
h5 { //description
  font-size: 16px;
  letter-spacing: 0.5px;
}
h6 { //button & navigation & footer
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}
p, li {
  font-size: 16px;
  line-height: 1.5;
  width: 100%;
  &.note {
    font-style: italic;
    color: $black-50;
  }
}

@media (min-width: $screenS) {
  h1 { font-size: 32px; }
  h2 { font-size: 28px; }
  h3 { font-size: 22px; }
  h4 { font-size: 20px; }
}

@media (min-width: $screenL) {
  h1 { font-size: 40px; }
  h2 { font-size: 32px; }
  h3 { font-size: 26px; }
  h4 { font-size: 22px; }
  h5 { font-size: 18px; }
  h6 { font-size: 16px; }
  p, li { font-size: 18px; }
}

@media (min-width: $screenXL) {
  h1 { font-size: 44px; }
  h2 { font-size: 36px; }
}
